
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  position: relative;
  width: 100vw;
  height: 100vh;
  min-width: 54rem;
  min-height: 44rem;
  font-family: "pokemon font", sans-serif;
  font-size: 1.5rem;
  background-color: black;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
#app {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 54rem;
  height: 44rem;
  background-color: white;
  margin: 0 auto;
  padding: 2rem;
}

.battle {
  display: grid;
  grid-gap: .5rem;
  grid-template-rows: 13rem 13rem 14rem;
  grid-template-columns: 24rem 2rem 24rem;
  grid-template-areas: "gauge1 gauge1 monster1" "monster2 gauge2 gauge2" "serif serif serif";
}

.gauge-enemy {
  grid-area: gauge1;
  height: 100%;
  position: relative;
}
.gauge-enemy .gauges {
    width: 95%;
    height: 70%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 30px 1fr;
}
.gauge-enemy .gauges .gauge {
      position: relative;
      border-bottom: .3rem solid #333;
      border-radius: 0 0 0 1.5rem;
      border-left: 1rem solid #333;
}
.gauge-enemy .gauges .gauge:after {
        content: "";
        position: absolute;
        bottom: -0.3rem;
        right: -1rem;
        border-top: .5rem solid transparent;
        border-left: 1rem solid #333;
        border-bottom: .5rem solid #333;
        border-right: 1rem solid transparent;
}

.name h2 {
  font-size: 2rem;
  font-weight: normal;
  letter-spacing: .12rem;
  padding-left: 2rem;
}
.name h2 span {
    font-size: 1.5rem;
    font-weight: bold;
}

.hps {
  margin: 0 0 0 1rem;
  border-right: solid 1rem #333;
  border-bottom: solid .2rem #333;
  display: grid;
  grid-template-columns: 4.5rem 1fr;
}
.hps .hp {
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    background-color: #333;
    color: yellow;
}
.hps .hp-gauge {
    position: relative;
    top: .8rem;
    height: 45%;
    background-color: green;
}

.gauge-me {
  position: relative;
  grid-area: gauge2;
  height: 100%;
}
.gauge-me .gauges {
    position: absolute;
    bottom: 0;
    width: 95%;
    height: 70%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 3rem 1fr;
}
.gauge-me .gauges .gauge {
      position: relative;
      border-bottom: .3rem solid #333;
      border-radius: 0 0 1.5rem 0;
      border-right: 1rem solid #333;
}
.gauge-me .gauges .gauge:after {
        content: "";
        position: absolute;
        bottom: -0.3rem;
        left: -1rem;
        border-top: .5rem solid transparent;
        border-left: 1rem solid transparent;
        border-bottom: .5rem solid #333;
        border-right: 1rem solid #333;
}

.enemy-enter-active, .enemy-leave-active {
  -webkit-transition: opacity 1s, -webkit-transform .5s;
  transition: opacity 1s, -webkit-transform .5s;
  transition: transform .5s, opacity 1s;
  transition: transform .5s, opacity 1s, -webkit-transform .5s;
}
.enemy-enter, .enemy-leave-to {
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  opacity: 0;
}
.monster-enemy {
  grid-area: monster1;
  width: 70%;
  height: 100%;
  margin: 0 auto;
}
.monster-enemy img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.me-enter-active, .me-leave-active {
  -webkit-transition: opacity .8s, -webkit-transform .5s;
  transition: opacity .8s, -webkit-transform .5s;
  transition: transform .5s, opacity .8s;
  transition: transform .5s, opacity .8s, -webkit-transform .5s;
}
.me-enter, .me-leave-to {
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  opacity: 0;
}
.monster-me {
  grid-area: monster2;
  width: 60%;
  height: 100%;
  margin: 0 auto;
  border-radius: 50%;
  background-color: black;
}
.monster-me img {
    opacity: .75;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 50%;
}

.serif {
  grid-area: serif;
  position: relative;
  height: 100%;
  padding: .2rem;
  border-radius: 1rem;
  border: solid .2rem #333;
}
.serif .wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    border: solid .2rem #333;
    border-top: solid .5rem #333;
    border-bottom: solid .5rem #333;
}
.serif .wrapper .content {
      position: absolute;
      bottom: 0;
      padding: .5rem 2rem;
      font-size: 2.4rem;
      line-height: 2.0;
}
.serif .wrapper .content a {
        color: blue;
}
.serif .wrapper .content a:hover {
          text-decoration: underline;
}

.command {
  position: absolute;
  top: -0.5rem;
  right: -0.2rem;
  width: 25rem;
  height: 15rem;
  padding: .2rem;
  border-radius: 1rem;
  border: .2rem solid #333;
  -webkit-box-shadow: 0 0 1rem #fff;
          box-shadow: 0 0 1rem #fff;
  background-color: #fff;
}
.command .wrapper {
    display: grid;
    padding: 3rem 1rem 1rem;
    font-size: 2.4rem;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
}

@-webkit-keyframes flash {
50% {
    opacity: 0;
}
}
@keyframes flash {
50% {
    opacity: 0;
}
}
.cmd:hover {
  -webkit-animation: flash .7s ease-in-out infinite;
          animation: flash .7s ease-in-out infinite;
  cursor: pointer;
}

@-webkit-keyframes flash {
50% {
    opacity: 0;
}
}
@keyframes flash {
50% {
    opacity: 0;
}
}
.serif-next {
  position: absolute;
  bottom: -2rem;
  right: 2rem;
  border: 2rem solid transparent;
  border-top: 2rem solid #333;
  -webkit-animation: flash 1s ease-in-out infinite;
          animation: flash 1s ease-in-out infinite;
}
.serif-next:hover {
    cursor: pointer;
}

@-webkit-keyframes flash {
50% {
    opacity: 0;
}
}
@keyframes flash {
50% {
    opacity: 0;
}
}
.item:hover {
  -webkit-animation: flash .7s ease-in-out infinite;
          animation: flash .7s ease-in-out infinite;
  cursor: pointer;
}
.item a {
  height: 6rem;
  padding: .5rem 1rem;
  display: grid;
  grid-template-columns: 5rem 20rem 1fr;
  grid-gap: .5rem;
}
.item a img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
.item a .item-gauge .item-hp {
    text-align: right;
    padding-right: 2rem;
}
@font-face {
  font-family: "pokemon font";
  src: url(/portfolio/static/fonts/pokemon-font.0abc39e.ttf); }
